<template id="fruits">
  <div class="fruits">
    <div class="fruits-list" v-if="showfruit" >
      <div class="list-header">
        <div class="header-f">
          <span></span>
        </div>
        <div class="header-th" v-for="item in months" :key="item.id" >
          <span :class="'th-' + item.color" >{{item.name}}</span>
        </div>
      </div>
      <div class="list-body" v-for="item in fruitsSort" :key="item.id" >
        <div class="body-f">
           <img :src="item.img" style="width:30px;max-height:20px;margin-right: 5px;" alt=""> <span>{{item.name}}</span>
        </div>
        <div class="body-td" v-for="m in fruitsMonth(item.months)" :key="m.id" >
          <span v-if="m.check" :class="['td-' + item.color,m.start ? 'td-start':'',m.end ? 'td-end':'']" ></span>
        </div>
      </div>
    </div>
    <div class="month-list">
      <div class="month-item" v-for="(month,index) in months" :key="month.id" >
        <div class="month-header">
          {{month.name}}
        </div>
        <div class="month-body">
          <div class="body-img" v-for="item in list[index]" :key="" >
            <img :src="item.img" alt="" >
            <span>{{item.name}}</span>
          </div>
        </div>
      </div> 
    </div>
  </div>
</template>

<script>
import fruits from '../json'
export default {
  data () {
    return {
      showfruit: false,
      months: [{
        id: 1,
        name: '一月',
        type: 'spring',
        color: 'pink'
      }, {
        id: 2,
        name: '二月',
        type: 'spring',
        color: 'pink'
      }, {
        id: 3,
        name: '三月',
        type: 'spring',
        color: 'pink'
      }, {
        id: 4,
        name: '四月',
        type: 'summer',
        color: 'green'
      }, {
        id: 5,
        name: '五月',
        type: 'summer',
        color: 'green'
      }, {
        id: 6,
        name: '六月',
        type: 'summer',
        color: 'green'
      }, {
        id: 7,
        name: '七月',
        type: 'autumn',
        color: 'orange'
      }, {
        id: 8,
        name: '八月',
        type: 'autumn',
        color: 'orange'
      }, {
        id: 9,
        name: '九月',
        type: 'autumn',
        color: 'orange'
      }, {
        id: 10,
        name: '十月',
        type: 'winter',
        color: 'blue'
      }, {
        id: 11,
        name: '十一月',
        type: 'winter',
        color: 'blue'
      }, {
        id: 12,
        name: '十二月',
        type: 'winter',
        color: 'blue'
      }],
      fruits: [],
      list:[[],[],[],[],[],[],[],[],[],[],[],[]]
    }
  },
  created () {
    this.fruits = fruits
    for (var i = 0; i < fruits.length; i++) {
      for (var j = 0; j < fruits[i].months.length; j++) {
        this.list[fruits[i].months[j] - 1].push(fruits[i])
      }
    }
    console.log(this.list);
  },
  methods: {
    fruitsMonth (list) {
      let rl = []
      for (let i = 0; i < 12; i++) {
        rl.push({
          id: i,
          check: false
        })
        for (var j = 0; j < list.length; j++) {
          if (i === list[j] - 1) {
            rl[i].check = true
            break
          }
        }
      }
      for (let i = 0; i < rl.length; i++) {
        if (rl[i].check) {
          rl[i].start = true
        }
        if (i > 0) {
          if (rl[i - 1].check) {
            rl[i].start = false
          }
        }
        if (i < rl.length - 1) {
          if (!rl[i + 1].check) {
            rl[i].end = true
          }
        }
        if (i === rl.length - 1 && rl[i].check) {
          rl[i].end = true
        }
      }
      return rl
    }
  },
  computed: {
    fruitsSort () {
      let fs = []
      fs = this.fruits.sort(function (a, b) {
        return a.months[0] - b.months[0]
      })
      return fs
    }
  }
}
</script>

<style scoped>
  .fruits {

  }
  .fruits-list {
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  .list-header {
    background: #f4f4f4;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .header-f {
    min-width: 120px;
    padding: 5px 10px;
    height: 30px;
    border-right: 1px solid #e5e5e5;
  }
  .header-th {
    min-width: 50px;
    padding: 5px 10px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border-right: 1px solid #e5e5e5;
  }
  .list-body {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-top: 1px solid #f4f4f4;
  }
  .body-f {
    min-width: 120px;
    padding: 5px 10px;
    height: 20px;
    border-right: 1px solid #e5e5e5;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .body-f span {
    font-size: 14px;
  }
  .body-td {
    min-width: 71px;
  }
  .body-td span {
    width: 100%;
    display: block;
    height: 8px;

  }

  .th-pink {
    color: #fd73c2
  }
  .th-green {
    color: #6adc80
  }
  .th-orange {
    color: #fdc973
  }
  .th-blue {
    color: #73d3fd
  }
  .td-start {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  .td-end {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .td-red {
    background: #ff7575
  }
  .td-yellow {
    background: #f5f449
  }
  .td-orange {
    background: #ea6c08
  }
  .td-green {
    background: #58c14f
  }
  .td-black {
    background: #443c3c
  }
  .td-white {
    background: #d6d6d6
  }
  .td-brown {
    background: #5d2411
  }
  .td-pink {
    background: #f572c3
  }
  .td-purple {
    background: #3d0446
  }
  .td-blue {
    background: #089bea
  }

  .month-list {
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  .month-item {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #f4f4f4;
  }
  .month-header {
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: center;

  }
  .month-body {
    flex: 1;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 20px;
    padding-bottom: 0px;
    padding-right:0px;
    border-left: 1px solid #ccc;
  }
  .body-img {
    padding: 2px;
    border: 1px solid #f4f4f4;
    width: 80px;
    margin-right: 20px;
    margin-bottom: 10px;
  }
  .body-img img {
    width: 80px;
  }
  .body-img span {
    font-size: 14px;
  }
</style>



